<template>
  <div class="home">
    <title-top class="title">
      <template v-slot:center>首页</template>
    </title-top>
    <div class="bac"></div>
    <!-- 内容区域 -->
    <div ref="sw" class="sw">
      <div class="content">
        <topHome :banner="banner" :recommend="goods"></topHome>
        <tabC :data="tabC" @tabC="tabCFun" ></tabC>
        <contantHome :categor="categor"></contantHome>
      </div>
    </div>
  </div>
</template>

<script>
import titleTop from "@/components/public/titleTop";

import { homeNet, homeCate } from "@/network/home";
import { ref, reactive, onMounted } from "vue";

import contantHome from "@/components/prive/home/contantHome";
import topHome from "@/components/prive/home/topHome";
import tabC from "@/components/public/tabC";

import BetterScroll from "better-scroll";
export default {
  name: "home",
  components: {
    titleTop,
    topHome,
    tabC,
    contantHome,
  },
  data() {
    return {
      tabC: ["畅销", "新书", "精选"],
      better: null,
    };
  },
  created() {
    this.$nextTick(() => {
      this.scrollBt()
    });
  },
  methods: {
    scrollBt() {
      this.better = new BetterScroll(this.$refs.sw, {
        probeType: 3,
        click: true,
        pullUpLoad: true,
      });
      this.better.on("pullingUp", () => {
        console.log(this.better);
        this.better.finishPullUp()
      });
    },
    
  },
  mounted(){
    
  },
  setup() {
    // 数据获取
    let banner = ref([]);
    let goods = ref([]);
    let categor = ref([]);
    let indexT = ["sales", "recommend", "new"];
    onMounted(() => {
      homeNet().then((res) => {
        banner.value = res.slides;
        goods.value = res.goods.data;
      });
    });
    const homeCateF = (type, page) => {
      homeCate(type, page).then((res) => {
        categor.value = res.goods.data;

      });
    };
    homeCateF("sales");

    const tabCFun = (res) => {
      homeCateF(indexT[res]);
    };
    // 滚动

    return { banner, categor, goods, tabCFun };
  },
};
</script>

<style scoped>
.home {
  width: 100%;
  height: 100vh;
  position: relative;
}
.title {
  height: 0.8rem;
  background-color: #43ba86;
  color: #fff;
  font-size: 0.4rem;
  font-weight: bold;
}
.bac {
  height: 0.8rem;
  width: 100%;
}
.content {
  width: 100%;
}
.sw {
  position: absolute;
  width: 100%;
  top: 0.8rem;
  bottom: 0.85rem;
}
</style>