<template>
  <div id="tabC">
      <div class="tab-item" v-for="(item,k) in data" :key="k" @click="tabFun(k)">
          <span :class="[{active:index == k}]">{{item}}</span>
      </div>
  </div>
</template>

<script>
export default {
    name:"tabC",
    data(){
        return{
            index:0
        }
    },
    props:{
        data:Array
    },
    methods:{
        tabFun(index){
            this.index = index
            this.$emit("tabC",index)
        }
    }
}
</script>

<style scoped>
 #tabC{
     display: flex;
     background-color: #fff;
     margin-top: 0.2rem;
     padding: 0.2rem 0;
     color: #555;
     cursor: pointer;
 }
 .tab-item{
     flex: 1;
     text-align: center;
 }
 .active{
     color: #43ba86;
     border-bottom: 2px solid #43ba86 ;
     padding-bottom: 0.1rem;
 }
</style>